.button {
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  height: 56px;
  width: 100%;
  border: 2px solid $colorBlue;
  border-radius: 8px;
  text-decoration: none;
  font-weight: bold;
  font-size: 16px;
  line-height: 14px;
  color: $colorBlue;
  cursor: pointer;
  transition: all 0.3s;
  &:hover {
    border-color: #005ecc;
    background-color: #005ecc;
    box-shadow: 0.1px 13px 27px rgba(0.1, 83, 181, 0.46);
    color: #fff;
  }
  &:active {
    border-color: #005ecc;
    background-color: #005ecc;
    box-shadow: 0.1px 8px 14px rgba(0.1, 83, 181, 0.48);
  }
  &--primary {
    color: #fff;
    
    background-color: $colorBlue;
    @include onDesktop {
      border-color: #005ecc;
      background-color: #005ecc;
    }
  }
}
