.about {
  &__paragraph {
    font-weight: normal;
    font-size: 16px;
    line-height: 27px;
    color: #3e3e3e;
    margin-bottom: 27px;
  }
  &__paragraph--hard {
    font-weight: bold;
    font-size: 16px;
    line-height: 27px;
    color: #3e3e3e;
    margin: 0;
  }
  &__subtitle {
    display: block;
    text-decoration: none;
    margin-bottom: 16px;
    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    color: $colorBlue;
  }
  &__title {
    margin: 0 0 48px;
    font-weight: bold;
    font-size: 36px;
    line-height: 36px;
    align-items: center;
    letter-spacing: -1.5px;
    color: #000;
    @include onDesktop {
      font-size: 48px;
      line-height: 48px;
    }
    &--link {
      text-decoration: none;
      color: $colorBlue;
    }

  }
}
