.contact-us {
  &__info {
    margin-bottom: 24px;
    &--last {
      margin-bottom: 32px;
    }
    &--title {
      font-weight: normal;
      font-size: 14px;
      line-height: 27px;
      color: #9fadc2;
      margin-bottom: 8px;
    }
    &--link {
      text-decoration: none;
      font-weight: normal;
      font-size: 16px;
      line-height: 27px;
      color: #3e3e3e;
    &:hover {
      color: #7373f1;
    }
    }
  }
  &--fild {
    margin: 0 0 16px;
    &--last {
      margin-bottom: 32px;
    }
  }
}
