h2.h1 {
  margin: 0;
}

.page {
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 27px;
  color: #3e3e3e;
  scroll-behavior: smooth;
  &__body {
    margin: 0;
    &--with-menu {
        overflow: hidden;
    }
  }
  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0%;
    transform: translate(-100%);
    transition: transform 0.3s;
    &:target {
      transform: translate(0);
    }
  }
  &__section {
    margin-bottom: 64px;
    @include onDesktop {
      margin-bottom: 104px;
    }
  }
  &__section--title {
    margin: 0 0 32px;
    font-weight: bold;
    text-align: center;
    font-size: 36px;
    line-height: 36px;
    letter-spacing: -1.5px;
    color: #000;
    @include onDesktop {
      margin-bottom: 56px;
      font-size: 48px;
      line-height: 48px;
    }
    &::after {
      content: "";
      background-image: url(../images/crown.svg);
      margin: 4px auto 0;
      display: block;
      height: 16px;
      width: 23px;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
    }
  }
}
