.place {
  &__photo--container {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    max-height: 256px;
    overflow: hidden;
    transition: linear 300ms;
    transition-property: transform;
    &:hover {
      transform: scale(1.05) ;
    }
    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    }

    @include onDesktop {
      height: 256px;
      padding-bottom: 0;
    }
  }
  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    border-radius: 8px;

  }

  &__link {
    font-weight: bold;
    text-decoration: none;
    color: $colorBlue;
  }
  &__photos {
    margin-bottom: 16px;
    @include onDesktop {
      margin-bottom: 24px;
    }
  }
}
