.footer {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  height: 320px;
  background-size: cover;
  background-position: center;
  background-image: url(../images/footer.jpg);
  @include onDesktop {
    height: 360px;
  }
  &__image {
    margin-bottom: 24px;
    @include onDesktop {
      margin-bottom: 36px;
    }
  }
}
