.testimonia {
  &__item {
    margin-bottom: 48px;
    @include onDesktop {
      margin: 0;
    }
    &:last-child {
      margin: 0;
    }
  }
  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;

  }
  &__photo {
    display: block;
    box-sizing: border-box;
    height: 150px;
    width: 150px;
    border-radius: 50%;
    margin-bottom: 28px;
    transition: linear 300ms;
    transition-property: transform;
    &:hover {
      transform: scale(1.1) ;
    }
    @include onDesktop {
      margin-bottom: 32px;
    }
  }
  &__name {
    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    color: $colorBlue;
    margin-bottom: 4px;
  }
  &__position {
    font-size: 14px;
    line-height: 27px;
    color: #9fadc2;
  }
  &__position--credo {
    text-align: center;
  }
}
